canvas {
  border-radius: 13px;
  cursor: url(./icons/pencil.png) 0 34, auto;
}

::-webkit-scrollbar {
  width: 0;
}
.author {
  padding-left: 3px;
  font-size: 11px;
  font-family: "Shadows Into Light", cursive;
}

.colors {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 500px;
  background-color: #fddc5c;
  border-radius: 13px;
}

.color {
  width: 32px;
  height: 32px;
  margin: 9px;
  cursor: pointer;
  border: inset -3px solid #fdfdfe;
  box-shadow: inset -2px -2px black;
  border-radius: 21px;
}

.colors-panel {
  position: relative;
  z-index: 10;
  margin-top: -10px;
}

.title-bar-text {
  color: #fddc5c;
  padding-bottom: 10px;
  cursor: default;
}

.tool-container {
  padding-bottom: 21px;
}
